import React, { Component } from 'react'
import {getNowPath} from '../function/someFun'
import {SmallNav} from '../../../components'


export default class ShowCapsuleItem extends Component {
    constructor(){
        super()
        this.state={
            dataSource:[],
            isLoading:false,
            smNavdata:{
                navData:[],
                nowData:''
            }
        }
    }
    componentDidMount(){
        this.getRoute()
    }
    getRoute = ()=>{
        const routeArr = getNowPath(this.props.history.location.pathname)
        this.setState({
            smNavdata:{
                navData:routeArr.nav,
                nowData:routeArr.detailRoute,
            }
        })
    }
    render() {
        return (
            <>
                <SmallNav smNavdata={this.state.smNavdata}  />
                <div className="f-content">
                    <div className="page-content">
                        <div className="myNormalFont capDetailHead" >
                            <div className="capDetailTitle">
                               追加10亿，设立15亿元“战疫基金”,斩获多国最高安全资质
                            </div>
                            <div className="capDetailTime" >
                                2020.03.02
                            </div>
                        </div>
                        <div className="capDetailContent">
                            <p>
                            产业互联网发展步入全球化、系统化的时代，信息安全被赋予新的定义和范畴。
                            </p>
                            <p>为了协助更多云上企业构建全球化的安全体系、实现业务的降本增效，腾讯云在全球范围内推动安全合规的进程，夯实信息安全管理和安全防护的基础建设，已成功斩获多个国家的最高等级安全资质，为企业发展开辟安全合规的航线。</p>
                        </div>  
                    </div>

                </div>

            </>

        )
    }
}
