const getNowPath = (pathname)=>{
    var smNav = []
    const location = pathname
    var detRoute = ''
    smNav = location.substr(1).split('/')
    smNav.shift()
    if(smNav.length ===3){   //  说明有 详细路由的存在
        detRoute = pathname
        smNav.length = 2
    }
    const navData = {
        nav: smNav,
        detailRoute: detRoute
    }
    return navData
}

export {
    getNowPath
}